<!--suppress HtmlUnknownTag, HtmlUnknownAttribute -->
<template>
  <div>
    <h3>Out-of-Context Hillwalkers' Quotes</h3>
    <p>
      A pseudo-random collection of the funniest and most accidentally spoken words and adages by our club members are here for your enjoyment:
    </p>

    <Notifications :alerts="alerts" />

    <v-card>
      <v-card-text v-if="quoteVisible" style="min-height:250px">
        <span v-for="line in this.quotes[this.current]">{{ line }}<br></span>
      </v-card-text>

      <v-card-actions>
        <v-btn :disabled="!quotes.length"
               :loading="!!restCallsGoing"
               @click="nextQuote()">
          {{ buttonText }}
        </v-btn>
      </v-card-actions>
    </v-card>
  </div>
</template>

<script>
import axios from 'axios';
import AxiosHelper from '../components/AxiosHelper.vue';
import Notifications from '../components/Notifications.vue';

function swap (arr, i, j) {
  let temp = arr[i];
  arr[i] = arr[j];
  arr[j] = temp;
}

function shuffleBatches (data) {
  for (let i=0; i < data.length; i++)
    for (let j=0; j < data[i].length; j++)
      swap (data[i],
        Math.floor(Math.random() * data[i].length),
        Math.floor(Math.random() * data[i].length));
}

function flattenIntoModel(data, model) {
  for (let i = data.length-1; i >= 0; i--)
    for (let j=0; j < data[i].length; j++)
      model.push(data[i][j]);
}

function nextQuote () {
  this.current = (this.current + 1) % this.quotes.length;
  this.blankOrMore = ' more';
}

export default {
    name: 'SUHC_Quotes',
    components: {Notifications},
    mixins: [AxiosHelper],

    data: () => ({
      blankOrMore: '',
      current: -1, // Ugh hacks
      quotes: [],
      nextQuote
    }),

    computed: {
      quoteVisible () {
        return this.current >= 0;
      },

      buttonText () {
        return this.quoteVisible ? 'More' : 'Show me!';
      }
    },

    mounted () {
      this.axiosHelper('retrieving quotes', () => axios.get(this.$dataUrl + '/suhc/quotes.json'), data => {
        shuffleBatches(data);
        flattenIntoModel(data, this.quotes);
      });
    }
}

</script>
